.section__left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
}

.dropdown__elem__list {
  list-style: none;
  width: 95%;
}
.dropdown__elem__list .dropdown__con {
  padding: 10px;
  border-bottom: 1px solid rgb(20, 20, 20);
  margin-bottom: 5px;
}

.dropdown__title__con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: min-content;
  width: 100%;
  padding: 1px 5px;
}

.elem__title {
  margin-block-start: 0.2em;
  margin-block-end: 0.5em;
}

.half__inputs__con {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.half__inputs__con.input__half__elem {
  display: flex;
  flex-wrap: wrap;
}

.input-half {
  width: 49%;
}

.dropdown__content {
  padding: 5px;
}

.add__input__elem__con {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.add__input__elem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border: 1px solid rgb(20, 20, 20);
  border-radius: 3px;
  padding: 7px;
  margin: 10px;
  font-size: 17px;
  width: fit-content;
  transition: border 0.1s ease;
}
.add__input__elem:hover, .add__input__elem:focus {
  border: 1px solid rgb(76, 0, 197);
}

.range__input__con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 70%;
}
.range__input__con .input__slider__elem {
  position: relative;
  width: 75%;
  height: 5px;
  border: 1px solid rgb(65, 60, 60);
  border-radius: 10px;
  background-color: rgb(190, 181, 181);
}
.range__input__con .input__slider__elem::before {
  content: "";
  position: absolute;
  top: -10px;
  left: calc(0% - 10px);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: rgb(76, 0, 197);
}

.create__description__input__elem {
  min-height: 150px;
}

.content__creator__con {
  border: 1px solid black;
  border-radius: 1em;
  padding: 15px;
  transition: border 0.1s ease;
}
.content__creator__con:hover, .content__creator__con:focus {
  border: 1px solid rgb(76, 0, 197);
}

.content__creator-date-place {
  height: 100%;
}
.content__creator-date-place label {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}

.creator__buttons__con {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 5px;
}
.creator__buttons__con .creator__done__btn {
  padding: 5px 15px;
  background-color: rgb(76, 0, 197);
  color: rgb(240, 232, 232);
  display: flex;
  justify-items: space-between;
}
.creator__buttons__con .creator__done__btn .material-symbols-outlined {
  margin-left: -5px;
}
.creator__buttons__con .creator__deleate__btn {
  margin-right: 1%;
}

input,
textarea {
  width: 100%;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: rgb(224, 224, 224);
  padding: 7px;
  margin-bottom: 5px;
  font-size: 17px;
  resize: none;
  transition: border 0.1s ease;
}
input:hover, input:focus,
textarea:hover,
textarea:focus {
  border: 1px solid rgb(76, 0, 197);
}

button {
  border: 1px solid rgb(65, 60, 60);
  border-radius: 5px;
  background: transparent;
  padding: 5px;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  transition: border 0.1s ease;
}
button:hover, button:focus {
  border: 1px solid rgb(76, 0, 197);
}

.material-symbols-outlined {
  font-size: 20px;
}